  --hero-bg-color: #ffb8ca;
  --hero-circle-bg-color: #ffa5ba;
  --title-color: #d41443;
  --pre-title-color: #ffa5ba;
  --time-color: #ffa5ba;
  --pink-bg-color: #ffb8ca;
  --brown-bg-color: #f0d1a5;
  --green-bg-color: #c2e297;
  --prime-animation: cubic-bezier(.4, 0, .2, 1);
  --prime-tansition-timing: 250ms;
}

html {
